/* Inline elements */
.strong,
.bold {
  font-weight: bold;
}
.emphasis,
.italic {
  font-style: italic;
}
.smallcaps {
  font-variant: small-caps;
  letter-spacing: 0.02em;
}
.allsmallcaps {
  font-variant: small-caps;
  text-transform: lowercase;
  letter-spacing: 0.02em;
}

a {
  color: var(--active-color);
  text-decoration: none;
}
a:hover {
  border-bottom: 1px dotted #aaa;
}

.smallcapsheader {
  font-style: normal;
  font-variant: small-caps;
  text-transform: lowercase;
  letter-spacing: 0.02em;
}
