body {
  font-kerning: normal;
  font-family: var(--serif-font);
  font-variant-numeric: oldstyle-nums proportional-nums;
  font-size: 20px;
}

/* Sans serif */
#toc,
.kicker,
kicker,
h3,
h4,
h5,
h6,
figcaption {
  font-family: var(--sans-serif-font);
  font-feature-settings: "kern, onum";
}

/* Sans serif bold */
figcaption strong,
figcaption b,
caption strong,
caption b,
.footnotes strong,
.footnotes b {
  font-weight: 600;
}
/* figcaption strong, figcaption b {color: #444;} */

.kicker,
kicker,
h1 {
  font-weight: 500;
}

/* Monospace */
pre,
code {
  font-family: var(--monospace-font);
  font-size: 0.88em;
}

/* Typography color, weight and style */
@media (min-width: 400px), print {
  @supports (hyphens: auto) or (-webkit-hyphens: auto) or (-moz-hyphens: auto)
    or (-ms-hyphens: auto) {
    main p,
    main figcaption,
    main ol,
    main ul,
    main blockquote {
      text-align: justify; /* Needs lang attribute on parent element to work. */

      hyphens: auto;
      -webkit-hyphens: auto; /* Only Safari, not Chrome. */
      -moz-hyphens: auto;
      -ms-hyphens: auto;
    }
  }
}

.kicker,
kicker,
h1,
h2,
h3,
h4,
h5,
h6,
big,
small,
div {
  text-align: left;
}

h1,
p,
ul,
ol,
li,
blockquote {
  font-style: italic;
}
