/* BLOCK ELEMENTS */

/* Vertical seperation */

ul,
ol,
blockquote,
dl,
.highlighter-rouge {
  margin-top: var(--relative-line-height-10x);
  margin-bottom: var(--relative-line-height-10x);
}

li {
  margin-bottom: var(--relative-line-height-025x);
}

ul ul,
ul ol,
ol ol,
ol ul,
ul ul li,
ul ol li,
ol ol li,
ol ul li blockquote blockquote {
  margin-top: 0;
  margin-bottom: 0;
}

pre {
  margin-top: 0; /* Default user style is 1em */
}

/* Unordered and ordered lists */

ul,
ol {
  list-style-position: outside;
  padding-left: var(--root-line-height); /* Overriden, except for print */
}

ul {
  list-style-type: none;
}
ul > li {
  position: relative;
}
ul > li:before {
  content: "•";
  padding-right: 0.3em;
  position: absolute;
  top: -1px;
  left: -0.95em;
}

ul ul,
ul ol,
ol ol,
ol ul {
  font-size: 0.97em;
}

/* Blockqoute */

blockquote p {
  font-style: italic;
}
blockquote em,
blockquote i {
  font-style: normal;
}
blockquote blockquote {
  margin: var(--root-line-height-05x) var(--paragraph-indent);
}

blockquote {
  line-height: var(--relative-line-height);
}
blockquote p {
  font-size: 1.1em;
  line-height: var(--relative-line-height);
}
blockquote blockquote {
  font-size: 0.95em;
}

blockquote footer {
  font-size: 1em;
}
blockquote footer {
  margin-top: var(--root-line-height-05x);
  margin-bottom: var(--root-line-height-05x);
}
blockquote footer {
  font-variant: small-caps;
  letter-spacing: 0.02em;
}
blockquote footer em,
blockquote footer i {
  font-variant: normal;
  letter-spacing: 0;
  font-style: italic;
}

/* Dictionary */

dt {
  font-variant: all-small-caps;
  margin-top: var(--root-line-height-05x);
}

/* Pre */

div.highlighter-rouge {
  font-size: 1rem;
}
@media screen and (max-width: 600px) {
  div.highlighter-rouge {
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 400px) {
  div.highlighter-rouge {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 350px) {
  div.highlighter-rouge {
    font-size: 0.7rem;
  }
}

pre {
  font-size: 0.88em;
  line-height: var(--relative-line-height);
}

@media screen {
  pre {
    background-color: #f7fbfd;
    padding: var(--root-line-height-05x);
    overflow-y: auto;
  }
}

@media screen and (max-width: 600px) {
  pre {
    padding: 0;
    margin: 0;
  }
  div.highlighter-rouge {
    background-color: #f7fbfd;
    padding-top: var(--relative-line-height-075x);
    padding-bottom: var(--relative-line-height-075x);
  }
}

/* Math block */

.container mjx-container[jax="CHTML"][display="true"],
.container mjx-container[jax="SVG"][display="true"] {
  margin: 0;
} /* Reset default */

mjx-container[display="true"] {
  max-width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  padding-top: var(--root-line-height-10x);
  padding-bottom: var(--root-line-height-10x);
}

mjx-container[display="true"] + mjx-container[display="true"] {
  padding-top: var(--root-line-height-025x);
}

/* Math fixes */

body mjx-container {
  font-variant-numeric: normal;
}
mjx-container * {
  overflow: visible;
}

/* Remove accessability outline when clicking on equations */
mjx-container[tabindex]:focus,
body :focus mjx-container[tabindex] {
  display: inline-table;
  outline: none !important;
}

/* Horizontal line */

hr {
  margin-top: var(--root-line-height-15x);
  margin-bottom: var(--root-line-height-15x);
}

hr {
  width: 3.9rem;
  border: 0;
  height: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
  margin-left: 0; /* Overrided */
  margin-right: auto; /* Overrided */
}

/* FIGURE */

figure {
  margin-top: var(--text-margin);
  margin-bottom: var(--text-margin);
}

figure {
  max-width: 100%;
}
img {
  max-width: 100%;
}

figcaption {
  font-size: 0.9rem;
  margin-top: 0.4rem;
  color: #666;
}

figcaption strong,
figcaption b {
  font-size: 0.8em;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 500;
}

@media print, screen and (min-width: 600px) {
  figure.float-left {
    float: left;
    margin-top: var(--root-line-height-10x);
    margin-bottom: var(--root-line-height-10x);
    padding-right: var(--root-line-height-10x);
  }
  figure.float-right {
    float: right;
    margin-top: var(--root-line-height-10x);
    margin-bottom: var(--root-line-height-10x);
    padding-left: var(--root-line-height-10x);
  }

  figure.float-left figcaption {
    padding-left: 0;
    padding-right: 0;
  }
  figure.float-right figcaption {
    padding-left: 0;
    padding-right: 0;
  }
}

@media print, screen and (min-width: 800px) {
  figure.float-left {
    padding-left: 0;
  }
  figure.float-right {
    padding-right: 0;
  }
}

/* FIGCAPTION AND CAPTION */

figcaption strong::after,
figcaption b::after,
caption strong::after,
caption b::after {
  content: "  ";
}

/* RESPONSIVE IMAGES */

@media screen {
  .lazyload {
    opacity: 1;
    transition: opacity 0.15s linear;
  }
  .lazyload-not-loaded {
    opacity: 0;
  }
}

.imagecontainer {
  max-width: 100%;
  position: relative; /*background-color: #efefef;*/
}

.imagecontainer > * {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
}
.imagecontainer img,
.imagecontainer picture {
  width: 100%;
  height: 100%;
}

figure img,
.fluidbox__ghost,
/*figure.image-drop-shadow .fluidbox__ghost, */
img.image-drop-shadow {
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}

.fluidbox__ghost {
  background-color: #fff;
}
.fluidbox__ghost {
  opacity: 0;
} /* Hide before it is activated */

.no-image-drop-shadow img,
figure.float-left img,
figure.float-right img {
  box-shadow: none;
}

@media print, screen and (max-width: 750px) {
  figure img {
    box-shadow: none;
  }
}

figure > div > .imagecontainer-outer {
  margin-bottom: var(--root-line-height-05x);
}

.fluidbox__overlay {
  background-color: hsl(0, 0%, 100%) !important;
}

/* TABLE */

table {
  display: block;
  width: fit-content;
  max-width: 100%;
  box-sizing: border-box;

  overflow-x: auto;
  white-space: nowrap;

  margin-top: var(--root-line-height-15x);
  margin-bottom: var(--root-line-height-05x);
  padding-bottom: var(--root-line-height-10x);

  border-collapse: separate;
  border-spacing: 0 0;

  /* font-variant-numeric: tabular-nums; */
}

table,
table th,
table td {
  font-size: 0.9rem;
  line-height: 1.1;
}

@media screen and (max-width: 600px) {
  table {
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 400px) {
  table {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 350px) {
  table {
    font-size: 0.7rem;
  }
}

table caption {
  text-align: left;
  font-family: var(--sans-serif-font);
  font-size: 0.7rem !important;
  line-height: var(--root-line-height);
  margin-bottom: 0.3em;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 500;
}

thead tr:first-child td,
th {
  border-top: 1px solid #000;
}

thead tr:last-child td,
th {
  border-bottom: 1px solid #000;
}

th {
  font-weight: normal;
  font-variant: all-small-caps;
  letter-spacing: 0.02em;
}

tbody tr:last-child td,
tbody tr:last-child th {
  border-bottom: 1px solid #000;
}

td,
th {
  padding: 5px 20px 2px 0px;
  margin: 0;
}

tfoot td {
  font-size: 0.8em;
  line-height: 1.3;
  font-family: var(--sans-serif-font);
  font-style: italic;
  color: #555;
  word-wrap: break-word;
}

.all-left td,
.all-left th {
  text-align: left;
}
.col1-left td:nth-child(1),
.col1-left th:nth-child(1) {
  text-align: left;
}
.col2-left td:nth-child(2),
.col2-left th:nth-child(2) {
  text-align: left;
}
.col3-left td:nth-child(3),
.col3-left th:nth-child(3) {
  text-align: left;
}
.col4-left td:nth-child(4),
.col4-left th:nth-child(4) {
  text-align: left;
}
.col5-left td:nth-child(5),
.col5-left th:nth-child(5) {
  text-align: left;
}
.col6-left td:nth-child(6),
.col6-left th:nth-child(6) {
  text-align: left;
}
.col7-left td:nth-child(6),
.col7-left th:nth-child(7) {
  text-align: left;
}

.all-center td,
.all-center th {
  text-align: center;
}
.col1-center td:nth-child(1),
.col1-center th:nth-child(1) {
  text-align: center;
}
.col2-center td:nth-child(2),
.col2-center th:nth-child(2) {
  text-align: center;
}
.col3-center td:nth-child(3),
.col3-center th:nth-child(3) {
  text-align: center;
}
.col4-center td:nth-child(4),
.col4-center th:nth-child(4) {
  text-align: center;
}
.col5-center td:nth-child(5),
.col5-center th:nth-child(5) {
  text-align: center;
}
.col6-center td:nth-child(6),
.col6-center th:nth-child(6) {
  text-align: center;
}
.col7-center td:nth-child(6),
.col7-center th:nth-child(7) {
  text-align: center;
}

.all-right td,
.all-right th {
  text-align: right;
}
.col1-right td:nth-child(1),
.col1-right th:nth-child(1) {
  text-align: right;
}
.col2-right td:nth-child(2),
.col2-right th:nth-child(2) {
  text-align: right;
}
.col3-right td:nth-child(3),
.col3-right th:nth-child(3) {
  text-align: right;
}
.col4-right td:nth-child(4),
.col4-right th:nth-child(4) {
  text-align: right;
}
.col5-right td:nth-child(5),
.col5-right th:nth-child(5) {
  text-align: right;
}
.col6-right td:nth-child(6),
.col6-right th:nth-child(6) {
  text-align: right;
}
.col7-right td:nth-child(6),
.col7-right th:nth-child(7) {
  text-align: right;
}

/* FOOTNOTES */

sup {
  line-height: 0.9; /* Fix higher line-height than other lines. */
}

.footnotes-headline {
  font-size: 1.2rem;
  font-family: "Gill Sans";
  color: #444;
  margin-top: var(--root-line-height-10x);
  margin-bottom: var(--root-line-height-025x);
}

.footnotes-headline + .footnotes {
  margin-top: var(--root-line-height-025x);
}

.footnotes {
  margin-top: var(--root-line-height-10x);
  margin-bottom: var(--root-line-height-10x);
}
.footnotes ol {
  margin-top: 0;
  margin-bottom: 0;
}

.footnotes {
  padding: 0;
  font-size: 0.8em;
  line-height: 1.5;
  font-family: var(--sans-serif-font);
  color: #666;
}

.footnotes .allsmallcaps {
  font-weight: 600;
}

.footnotes p {
  text-align: left;
}

.footnotes ol {
  list-style-position: outside;
}

@media screen {
  .footnotes ol {
    padding-left: var(--text-margin);
    padding-right: var(--text-margin);
  }
}
@media screen and (max-width: 400px) {
  .footnotes ol {
    list-style-position: inside;
  }
}
@media print {
  .footnotes ol {
    padding-left: 0;
    padding-right: 0;
  }
}

/* Only Mac (not even iOS) includes Lucida Grande. Most emoji/unicode characters quite ugly by default. */
@font-face {
  font-display: block;
  font-family: "Lucida Grande";
  font-weight: 500;
  src: url("/assets/fonts/Lucida Grande/LucidaGrande.woff2");
}

.reversefootnote {
  font-family: "Lucida Grande", "Lucida Sans Unicode", "Cambria", sans-serif;
  font-size: 1em;
  line-height: 1em;
}
.reversefootnote:hover {
  border-bottom: none;
}
@media print {
  .reversefootnote {
    display: none;
  }
}

/* COPYRIGHT NOTICE */

.copyrightnotice {
  margin-top: var(--root-line-height-05x);
  margin-bottom: var(--root-line-height-05x);

  font-variant: all-small-caps;
  font-size: var(--root-line-height-05x);
  line-height: 1.3;
  letter-spacing: 0.02rem;
  font-weight: 500;
  color: #999;
  font-variant-numeric: normal;
  font-family: Gill Sans, sans-serif;
}

.copyrightnotice,
.copyrightnotice a,
.copyrightnotice a:hover {
  font-weight: 500;
  color: #999;
  border: 0;
}

/* Divide paragraphs by indention */

.divide-paragraphs-by-idention p {
  margin-top: 0;
  margin-bottom: 0;
}
.divide-paragraphs-by-idention p + p,
.divide-paragraphs-by-idention figure.float-left + p,
.divide-paragraphs-by-idention figure.float-right + p {
  text-indent: var(--paragraph-indent);
}

div.warning {
  margin-top: var(--root-line-height-05x);
  margin-bottom: var(--root-line-height-05x);
  font-size: 0.8em;
  line-height: 1.2rem;
  font-family: var(--sans-serif-font);
  color: #666;
}

div.warning strong {
  color: #444;
  font-weight: 600;
}
